<template>
    <div class="form">
        <el-form label-width="150px" style="margin:20px;width:90%;min-width:600px;"
                 :model="article_data"
                 :rules="rules"
                 v-loading="formLoading"
                 ref='refArticle'>
            <el-row>
                <el-col :span="6">
                    <el-form-item label="ios">
                        <el-input v-model="article_data.ios.version"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="ios">
                        <el-switch v-model="article_data.ios.forcible"></el-switch>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="ios" prop='ios_content'>
                <div id="description_ios" style="height:200px;"></div>
            </el-form-item>

            <el-row>
                <el-col :span="6">
                    <el-form-item label="android" >
                        <el-input v-model="article_data.android.version"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="android">
                        <el-switch v-model="article_data.android.forcible"></el-switch>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="android" prop='android_content'>
                <div id="description_android" style="height:200px;"></div>
            </el-form-item>

            <el-row>
                <el-col :span="6">
                    <el-form-item label="web" >
                        <el-input v-model="article_data.web.version"></el-input>
                    </el-form-item>
                </el-col>
                <el-col :span="6">
                    <el-form-item label="web">
                        <el-switch v-model="article_data.web.forcible"></el-switch>
                    </el-form-item>
                </el-col>
            </el-row>
            <el-form-item label="web"  prop='web_content'>
                <div id="description_web" style="height:200px;"></div>
            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click='onSubmit("refArticle")'>{{article_data.id ? '' : ''}}
                </el-button>
                <!--<el-button @click='reset_article("article_data")'></el-button>-->
            </el-form-item>
        </el-form>

    </div>
</template>
<script>
    import EditJs from './Edit.js';

    module.exports = EditJs;
</script>
