import Vue from 'vue';


/**
 * 
 */
import {
	plugins
} from 'plugins/';


/**
 * Vue
 */
Vue.use({
	install(Vue, options) {

		/**
		 * Vue
		 * @param  {object} target Vue
		 * @param  {object} source 
		 */
		var deepRegister = function(target, source) {
			for (var k in source) {
				if (typeof source[k] === 'object') {
					deepRegister(target, source[k]);
				} else {
					target.prototype['$$' + k] = source[k];
				}
			}
		}

		deepRegister(Vue, plugins);
	}
});


