import {
	store
} from '../../util/';


module.exports = {
	//
	userinfo: store.get('userinfo') || {},

	//
	//
	remumber: {
		remumber_flag: store.get('remumber_flag') ? true : false,
		remumber_login_info: store.get('remumber_login_info') || {
			username: '',
			token: ''
		}
	},
};


