var env = process.env;

var settings = {

	//
	gbs: {
		// host: '//slsadmin.api.' + (env.NODE_ENV === 'development' ? 'sls' : 'sailengsi') + '.com',
		host: '/api/v1', //slsadmin.api.sls.com,Nginx
		db_prefix: 'sls_admin_', //key
        grant_type:'password',
        client_id:2,
		client_secret:'srnXFMr6y6wft94cBc2Qj4dmEOoaNfXqerIIcax5',
        image_host:'http://images.17duu.com'
	},

	//
	cbs: {
		/**
		 * ajax200
		 * @param  {object} err 
		 */
		statusError(err) {
			if (err.status !== 404) {
				this.$message({
					showClose: true,
					message: '' + err.msg,
					type: 'error'
				});
			} else {
				this.$store.dispatch('remove_userinfo').then(() => {
					this.$alert(err.status + ',' + err.msg + '', '', {
						confirmButtonText: '',
						callback: action => {
							this.$router.push('/login');
						}
					});
				});
			}
		},

		/**
		 * ajax
		 */
		requestError(err) {
			this.$message({
				showClose: true,
				message: '' + (err.response ? err.response.status : '') + ',' + (err.response ? err.response.statusText : ''),
				type: 'error'
			});
		}
	}
};


module.exports = settings;


