<template>
    <div class="form">
        <el-form style="margin:20px;width:60%;min-width:600px;" 
            label-width="100px" 
            :model="user_data"
            :rules="user_rules"
            ref='user_data'>
            <el-form-item class='edit-form' 
                label="" 
                prop='email'>
                <el-input
                        :disabled='user_data.id ? true : false'
                        v-model="user_data.email" placeholder=''></el-input>
            </el-form-item>
            <el-form-item class='edit-form' 
                label="" 
                prop='username'>

                <el-input 
                    :disabled='user_data.id ? true : false'
                    v-model="user_data.username" 
                    placeholder=''></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-radio-group v-model="user_data.sex">
                    <el-radio label="1"></el-radio>
                    <el-radio label="2"></el-radio>
                    <el-radio label="3"></el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="">
                <el-col :span="11">
                    <el-date-picker style="width: 100%;" type="date" placeholder="" v-model="user_data.birthday" format='yyyy-MM-dd' @change='onChangeDate'></el-date-picker>
                </el-col>
            </el-form-item>
            <el-form-item class="edit-form" 
                label=''
                prop='address'>
                <el-input type='textarea' 
                    v-model="user_data.address"
                    placeholder=''></el-input>
            </el-form-item>
            <el-form-item label="">
                <el-switch on-text="" off-text=""
                    v-model="user_data.status"></el-switch>
            </el-form-item>
            <el-form-item label="" v-if='!user_data.id'>
                <strong>1234556</strong>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click='save_user("user_data")'>{{user_data.id ? '' : ''}}</el-button>
                <el-button type="default"
                           v-if="!user_data.id"
                           @click='reset_user("user_data")'></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import EditJs from './Edit.js';
    module.exports=EditJs;
</script>
<style scoped>
    .edit-form{
        width:500px;
    }
</style>



